* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  cursor: none !important;
}
@media (max-width: 992px) {
  *,
*::before,
*::after {
    cursor: auto !important;
  }
}

a {
  color: inherit;
  outline: none;
}

aside,
nav,
footer,
header,
section {
  display: block;
}

html,
body {
  height: 100%;
  min-width: 320px;
}

body {
  font-size: 16px;
  line-height: 1;
  font-family: "Oswald", sans-serif;
  color: #000;
  font-weight: 400;
  font-style: normal;
  line-height: 24px;
}

input,
button,
textarea {
  font-size: inherit;
  font-family: "Oswald", sans-serif;
}

button {
  cursor: pointer;
  background-color: transparent;
}

a,
a:visited {
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

.wrapper {
  width: 100%;
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1320px;
  padding: 0 15px;
  margin: 0 auto;
}
@media (max-width: 768px) {
  .container {
    padding: 0 1opx;
  }
}

.mouse {
  position: fixed;
  top: 0;
  left: 0;
  width: 45;
  height: 45;
  border-radius: 100%;
  z-index: 10000000;
  pointer-events: none;
}
.mouse::before {
  content: "";
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 35px;
  height: 35px;
  background: #D7D7D7;
  box-shadow: 4px 4px 4px #CCCACA;
  border-radius: 100%;
  transition: opacity 0.3s ease-in-out;
}
.mouse::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 55px;
  height: 55px;
  border-radius: 100%;
  background: #CDCDCD;
  transition: opacity 0.3s ease-in-out;
}
@media (max-width: 992px) {
  .mouse {
    display: none;
  }
}
.mouse__view {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 70px;
  height: 70px;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  line-height: 18px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #fff;
  background-color: #000;
  border-radius: 100%;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}

.view-visible .mouse__view {
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}

.links-visible::after {
  border: 4px solid #cdcdcd;
  transform: translate(-50%, -50%) scale(0.5);
  background-color: transparent;
  transition: all 0.3s ease 0s;
}
.links-visible::before {
  opacity: 0;
}

.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10000;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #B1B3B2;
}
.preloader__back {
  font-family: "Merriweather", sans-serif;
  font-weight: 300;
  font-size: 360px;
  line-height: 453px;
  color: #fff;
  opacity: 0;
  transform: translateX(7%);
  transition: opacity 0.3s ease-in-out 0s;
  animation: preloader 20s linear;
}
@media (max-height: 749.9px) {
  .preloader__back {
    font-size: 260px;
    line-height: 300px;
  }
}
.preloader__forward {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -10%);
  font-family: "Merriweather";
  font-weight: 300;
  font-size: 72px;
  line-height: 91px;
  opacity: 0;
  transition: all 0.6s cubic-bezier(0.77, 0.125, 0.265, 1.04) 0s;
}
@media (max-width: 992px) {
  .preloader__forward {
    width: 500px;
    text-align: center;
    font-size: 55px;
    line-height: 70px;
  }
}
@media (max-width: 576px) {
  .preloader__forward {
    width: 90%;
    font-size: 35px;
    line-height: 45px;
  }
}
.preloader__background {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 330px;
  height: 220px;
  z-index: -1;
}
.preloader__background::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background-color: #fff;
  transition: width 0.6s cubic-bezier(0.77, 0.125, 0.265, 1.04) 0s;
}

@keyframes preloader {
  0% {
    transform: translateX(7%);
  }
  100% {
    transform: translateX(-100%);
  }
}
.preloader-animation .preloader__back {
  opacity: 0.15;
}
.preloader-animation .preloader__forward {
  opacity: 1;
  transform: translate(-50%, -50%);
}
.preloader-animation .preloader__background::before {
  transition-delay: 0.5s;
  width: 330px;
}

.preloader-hidden .preloader__back {
  opacity: 0;
  transition: opacity 0.3s cubic-bezier(0.77, 0.125, 0.265, 1.04), transform 0.3s cubic-bezier(0.77, 0.125, 0.265, 1.04);
}
.preloader-hidden .preloader__background::before {
  width: 0;
}
.preloader-hidden .preloader__forward {
  opacity: 0;
  transition-delay: 0.5s;
  transform: translate(-50%, -50%);
  transition: opacity 0.3s ease-in-out;
}

.preloader-none {
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
}

