.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 150;
  min-width: 320px;
  padding: 30px 0;
  opacity: 0;
  transform: translateY(100px);
}
@media (max-width: 768px) {
  .header {
    padding: 25px 0;
  }
}
@media (max-width: 480px) {
  .header {
    padding: 20px 0;
  }
}
.header__body {
  display: flex;
  align-items: center;
}
.header__logo {
  display: inline-flex;
  align-items: center;
  text-transform: uppercase;
  letter-spacing: 1px;
  line-height: 24px;
  margin-right: 15%;
  width: 115px;
}
.header__logo-icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  flex: 0 0 37px;
  height: 37px;
  border-radius: 100%;
  background-color: #000;
  margin-right: 10px;
}
.header__logo-icon img {
  display: block;
  height: 18.5px;
  flex: 0 0 15.3px;
}
.header__list {
  display: flex;
  align-items: center;
}
@media (max-width: 768px) {
  .header__list {
    display: none;
  }
}
.header__item:not(:last-child) {
  margin-right: 63px;
}
.header__link {
  line-height: 24px;
  letter-spacing: 1px;
  text-transform: uppercase;
}
.header__burger-body {
  width: 60px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: auto;
}
@media (any-hover: hover) {
  .header__burger-body:hover .header__burger span:first-child {
    transform: translateX(-8px);
  }
  .header__burger-body:hover .header__burger span:last-child {
    transform: translateX(8px);
  }
}
.header__burger {
  width: 60px;
  height: 9px;
  border: none;
}
.header__burger span {
  display: block;
  width: 100%;
  height: 2px;
  background-color: #000;
  transition: transform 0.3s ease 0s;
}
.header__burger span:not(:last-child) {
  margin-bottom: 5px;
}

