.main-section
    position: relative
    display: flex
    flex-direction: column
    justify-content: center
    height: 100vh
    min-height: 750px
    transition: background-color 1s
    @media (max-height: 749.9px)
        min-height: 570px
    @media (max-width: $md3)
        padding-top: 100px
        height: auto
        min-height: 100vh
    @media (max-width: $md4)
        padding-top: 70px

    &__explore
        letter-spacing: 1px
        text-transform: uppercase
        opacity: 0
        transform: translateY(100px)
        @media (max-width: $md3)
            display: none

.slide-1
    background-color: #b1b3b2

.slide-2
    background-color: #f1f1f1

.slide-3
    background-color: #b5b7b4

.slide-4
    background-color: #b5b8c2

.slide-5
    background-color: #c4c3c0

.slides-container

    &__left
        position: relative
        flex: 1 0 48%
        padding-top: 67px
        @media (max-width: $md3)
            flex: 1 0 100%
            padding-top: 0
        @media (max-width: $md4)
            max-height: 230px

    &__slide
        position: absolute
        z-index: 20
        width: 100%
        left: 0
        top: calc(67px + 50px)
        pointer-events: none
        @media (max-width: $md3)
            top: -90px

    &__text
        width: 100%
        position: relative
        flex-shrink: 0

    &__title
        margin: 0
        margin-bottom: 70px
        font-family: 'Merriweather', sans-serif
        font-weight: 300
        font-size: 112px
        line-height: 130px
        color: $light-color
        opacity: 0
        transform: translateY(100px)
        @media (max-height: 749.9px)
            font-size: 100px
            line-height: 100px
            margin-bottom: 40px
        @media (max-width: 576px)
            font-size: 80px
            line-height: 80px
        @media (max-width: $md4)
            font-size: 50px
            line-height: 55px
            margin-bottom: 15px

    &__bg
        overflow: hidden
        position: relative
        flex: 1 0 57.37%
        height: 560px
        margin-left: -5.4%
        @media (max-height: 749.9px)
            margin-top: 40px
            flex: 1 0 52%
            height: 450px
        @media (max-width: $md3)
            flex: 1 0 100%
            min-height: 350px
            margin: 0
        @media (max-width: $md4)
            min-height: 250px

.block-wrapper
    position: relative
    z-index: 100
    display: flex
    justify-content: space-between
    @media (max-width: $md3)
        flex-direction: column-reverse
        justify-content: initial

.slide-active
    pointer-events: auto
    opacity: 1
    @media (max-width: $md3)
        position: relative

.designers-info
    display: flex
    opacity: 0
    transform: translateY(100px)

    &__image
        width: 190px
        height: 142px
        margin-right: 30px
        img
            height: 100%
            width: 100%
            object-fit: cover
        @media (max-width: $md2)
            width: 40%
            min-width: 150px
        @media (max-width: $md3)
            width: auto

    &__descr
        margin-left: 30px

    &__country
        letter-spacing: 1px
        text-transform: uppercase
        margin-bottom: 10px

    &__by
        font-size: 14px
        line-height: 18px
        letter-spacing: 1px
        text-transform: uppercase
        color: $grey-color

.dark
    color: $black-color

.slide-bg

    &__inner
        position: absolute
        top: 0
        right: 0
        width: 100%
        height: 100%
        overflow: hidden
        pointer-events: none
        opacity: 0

    &__link
        position: absolute
        right: 0
        top: 0
        width: 100%
        height: 100%
        background-size: cover
        background-repeat: no-repeat
        background-position: center

.inner-current
    opacity: 1
    z-index: 11
    pointer-events: auto

.index
    z-index: 10

.shapes
    position: absolute
    right: 0
    top: 0
    width: 33.5%
    height: 100%
    @media (max-width: $md3)
        display: none

    &__item
        position: absolute
        left: 0
        top: 0
        width: 100%
        height: 100%
        overflow: hidden
        opacity: 0

    &__content
        position: absolute
        top: 0
        right: 0
        width: 100%
        height: 100%
        overflow: hidden

.shapes-current
    opacity: 1
    z-index: 11

