.header
    position: absolute
    top: 0
    left: 0
    width: 100%
    z-index: 150
    min-width: $min-width
    padding: 30px 0
    opacity: 0
    transform: translateY(100px)
    @media (max-width: $md3)
        padding: 25px 0
    @media (max-width: $md4)
        padding: 20px 0

    &__body
        display: flex
        align-items: center

    &__logo
        display: inline-flex
        align-items: center
        text-transform: uppercase
        letter-spacing: 1px
        line-height: 24px
        margin-right: 15%
        width: 115px

    &__logo-icon
        display: inline-flex
        align-items: center
        justify-content: center
        flex: 0 0 37px
        height: 37px
        border-radius: 100%
        background-color: $black-color
        margin-right: 10px
        img
            display: block
            height: 18.5px
            flex: 0 0 15.3px

    &__list
        display: flex
        align-items: center
        @media (max-width: $md3)
            display: none

    &__item
        &:not(:last-child)
            margin-right: 63px

    &__link
        line-height: 24px
        letter-spacing: 1px
        text-transform: uppercase

    &__burger-body
        width: 60px
        height: 24px
        display: flex
        align-items: center
        justify-content: center
        margin-left: auto
        @media (any-hover: hover)
            &:hover
                .header__burger
                    span:first-child
                        transform: translateX(-8px)
                    span:last-child
                        transform: translateX(8px)

    &__burger
        width: 60px
        height: 9px
        border: none
        span
            display: block
            width: 100%
            height: 2px
            background-color: $black-color
            transition: transform 0.3s ease 0s
            &:not(:last-child)
                margin-bottom: 5px
