*
	padding: 0
	margin: 0
	border: 0

*,
*::before,
*::after
	box-sizing: border-box
	cursor: none !important
	@media (max-width: $md2)
		cursor: auto !important
a
	color: inherit
	outline: none

aside,
nav,
footer,
header,
section
	display: block

html,
body
	height: 100%
	min-width: $min-width

body
	font-size: 16px
	line-height: 1
	font-family: $font-family
	color: $black-color
	font-weight: 400
	font-style: normal
	line-height: 24px

input,
button,
textarea
	font-size: inherit
	font-family: $font-family

button
	cursor: pointer
	background-color: transparent

a,
a:visited
	text-decoration: none
	color: inherit

a:hover
	text-decoration: none

ul li
	list-style: none

h1,
h2,
h3,
h4,
h5,
h6
	font-weight: inherit
	font-size: inherit

.wrapper
	width: 100%
	overflow: hidden

// .main

// 	&__section
		// position: relative
		// padding-top: 97px

.container
	width: 100%
	max-width: $max-width-container
	padding: 0 15px
	margin: 0 auto
	@media (max-width: $md3)
		padding: 0 1opx

//Mouse
.mouse
	position: fixed
	top: 0
	left: 0
	width: 45
	height: 45
	border-radius: 100%
	z-index: 10000000
	pointer-events: none
	&::before
		content: ''
		position: absolute
		z-index: 2
		top: 50%
		left: 50%
		transform: translate(-50%, -50%)
		width: 35px
		height: 35px
		background: #D7D7D7
		box-shadow: 4px 4px 4px #CCCACA
		border-radius: 100%
		transition: opacity 0.3s ease-in-out
	&::after
		content: ''
		position: absolute
		left: 50%
		top: 50%
		transform: translate(-50%, -50%)
		width: 55px
		height: 55px
		border-radius: 100%
		background: #CDCDCD
		transition: opacity 0.3s ease-in-out

	@media (max-width: $md2)
		display: none

	&__view
		position: absolute
		left: 50%
		top: 50%
		transform: translate(-50%, -50%)
		width: 70px
		height: 70px
		z-index: 10
		display: flex
		align-items: center
		justify-content: center
		font-size: 18px
		line-height: 18px
		letter-spacing: 1px
		text-transform: uppercase
		color: $light-color
		background-color: $black-color
		border-radius: 100%
		opacity: 0
		transition: opacity 0.3s ease-in-out

.view-visible .mouse__view
	opacity: 1
	transition: opacity 0.3s ease-in-out

.links-visible
	&::after
		border: 4px solid #cdcdcd
		transform: translate(-50%, -50%) scale(0.5)
		background-color: transparent
		transition: all 0.3s ease 0s
	&::before
		opacity: 0

//Preloader
.preloader
	position: fixed
	top: 0
	left: 0
	width: 100%
	height: 100%
	z-index: 10000
	overflow: hidden
	display: flex
	justify-content: center
	align-items: center
	background-color: #B1B3B2

	&__back
		font-family: 'Merriweather', sans-serif
		font-weight: 300
		font-size: 360px
		line-height: 453px
		color: $light-color
		opacity: 0
		transform: translateX(7%)
		transition: opacity 0.3s ease-in-out 0s
		animation: preloader 20s linear
		@media (max-height: 749.9px)
			font-size: 260px
			line-height: 300px

	&__forward
		position: absolute
		left: 50%
		top: 50%
		transform: translate(-50%, -10%)
		font-family: 'Merriweather'
		font-weight: 300
		font-size: 72px
		line-height: 91px
		opacity: 0
		transition: all 0.6s cubic-bezier(0.770, 0.125, 0.265, 1.040) 0s
		@media (max-width: $md2)
			width: 500px
			text-align: center
			font-size: 55px
			line-height: 70px
		@media (max-width: 576px)
			width: 90%
			font-size: 35px
			line-height: 45px

	&__background
		position: absolute
		left: 50%
		top: 50%
		transform: translate(-50%, -50%)
		width: 330px
		height: 220px
		z-index: -1
		&::before
			content: ''
			position: absolute
			top: 0
			left: 0
			width: 0
			height: 100%
			background-color: $light-color
			transition: width 0.6s cubic-bezier(0.770, 0.125, 0.265, 1.040) 0s

@keyframes preloader
	0%
		transform: translateX(7%)

	100%
		transform: translateX(-100%)

//Animation
.preloader-animation
	.preloader__back
		opacity: 0.15
	.preloader__forward
		opacity: 1
		transform: translate(-50%, -50%)
	.preloader__background::before
		transition-delay: 0.5s
		width: 330px

.preloader-hidden
	.preloader__back
		opacity: 0
		transition: opacity 0.3s cubic-bezier(0.770, 0.125, 0.265, 1.040), transform 0.3s cubic-bezier(0.770, 0.125, 0.265, 1.040)
	.preloader__background::before
		width: 0
	.preloader__forward
		opacity: 0
		transition-delay: 0.5s
		transform: translate(-50%, -50%)
		transition: opacity 0.3s ease-in-out

.preloader-none
	opacity: 0
	visibility: hidden
	transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out
